import React, { Component } from "react";
import { Card, Container } from "react-bootstrap";
import OrderPlaceModal from "./OrderPlaceModal";
import OrderHistory from "./OrderHistory";
import Search from "./Search";
import { OrderAction, Server } from '../../services';

const server = new Server('/buyer/')
const orderAction = new OrderAction()
class Order extends Component {
    state = {
        orders: [],
        buyer: {},
        modalIsOpen: false,
        loading: false,
        editData: {},
        next: null,
        previous: null,
    }



    async componentDidMount() {
        window.scrollTo(0, 0)

        try {
            let buyer = await server.get()
            this.setState({
                buyer: buyer.data.results[0] || {},
            })
        } catch (error) {
            console.error(error)
        }
        this.getData()
    }


    getData = async (url = '') => {
        this.setState({
            loading: true,
            modalIsOpen: false
        })
        try {
            let { data } = await orderAction.get(url)
            this.setState({
                loading: false,
                orders: data.results,
                next: data.next,
                previous: data.previous
            })
        } catch (error) {
            this.setState({
                loading: false,
            })
            console.error(error)
        }
    }


    modalHandler = () => {
        this.setState(prevState => ({
            modalIsOpen: !prevState.modalIsOpen,
        }));
    }

    handleChange(event) {
        event.preventDefault();
        this.setState({ [event.target.name]: event.target.value });
    }

    orderApproveCancelHandler = async (id, status) => {
        this.setState({
            loading: true,
            modalIsOpen: false
        })

        let response = await orderAction.orderCancelConfirmed(id, status)
        if (response.isSuccess) {
            this.getData()
        }
    }


    setOrderToState = (loading, orders) => {
        this.setState({
            loading,
            orders
        })
    }

    render() {
        let { modalIsOpen, editData } = this.state

        return (
            <Container fluid={true} style={{ marginTop: 95 }}>
                <Card>
                    <Card.Header>
                        <h5><u>অর্ডার লিস্ট</u></h5>
                    </Card.Header>
                    <Card.Body>

                        <Search
                            modalHandler={() => this.setState({ modalIsOpen: !modalIsOpen, editData: {} })}
                            setOrderToState={this.setOrderToState}
                            getData={this.getData}
                        />

                        <OrderHistory
                            editHandler={(value) => this.setState({ editData: value, modalIsOpen: true })}
                            orderApproveCancelHandler={this.orderApproveCancelHandler}
                            stateData={this.state}
                            getData={this.getData}
                        />

                    </Card.Body>
                </Card>


                {modalIsOpen &&
                    <OrderPlaceModal
                        modalIsOpen={modalIsOpen}
                        modalHandler={() => this.setState({ modalIsOpen: !modalIsOpen, editData: {} })}
                        getData={this.getData}
                        editData={editData}
                    />}


            </Container>
        )
    }
}

export default Order;
