import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, FormGroup, Label, FormFeedback, Input } from "reactstrap";
import { Server } from "../../services";
import PropTypes from 'prop-types'
import { profileValidator } from "../../validators";




const server = new Server('/merchant_user_permission/')

let initState = {
    name: '',
    email: '',
    phone_number: '',
    address: '',
    password: '',
    confirm_password: '',
    error: {},
    loading: false,
    create_permission: true,
    delete_permission: false,
    owner: false,
    read_permission: true,
    update_permission: true,
}

class UserForm extends Component {
    state = initState

    componentDidMount() {
        if (!this.props.editData.id)
            return ''


        let { create_permission, delete_permission, owner, read_permission, update_permission, user: { name, email, phone_number, address } } = this.props.editData

        this.setState({
            loading: false,
            create_permission,
            delete_permission,
            owner,
            read_permission,
            update_permission,
            name,
            email,
            phone_number,
            address
        })
    }


    changeHandler = (e) => {
        this.setState(prevState => {
            let { error } = prevState

            if (error[e.target.name])
                delete error[e.target.name]

            return {
                [e.target.name]: e.target.value,
                error
            }
        });
    };


    submitHandler = async (e) => {
        e.preventDefault();
        let validate = profileValidator(this.state)
        if (!validate.isValid) {
            this.setState({ error: validate.error })
            return;
        }

        this.setState({ loading: true })

        let response
        if (this.props.editData.id) {
            response = await server.update(this.state, `/merchant_user_permission/${this.props.editData.id}/`)
        } else {
            response = await server.set(this.state)
        }

        if (response) {
            this.setState(initState)
            this.props.modalHandler()
            this.props.getData()
            return;
        }
        this.setState({ loading: false })
    }

    render() {
        let { modalOpen, modalHandler } = this.props;
        let { name, email, phone_number, address, password, confirm_password, error, loading, create_permission, delete_permission, owner, read_permission, update_permission } = this.state


        return (
            <Modal
                size="md"
                dialogClassName="modal-90w"
                show={modalOpen}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>ব্যবহারকারীর তথ্য</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={this.submitHandler}>
                        <FormGroup>
                            <Label for="name">
                                নাম<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="name"
                                value={name}
                                required
                                invalid={error.name ? true : false}
                            />
                            {error.name && <FormFeedback>{error.name}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="phone">
                                ফোন নম্বর<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="phone_number"
                                value={phone_number}
                                required
                                invalid={error.phone_number ? true : false}
                            />
                            {error.phone_number && <FormFeedback>{error.phone_number}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="email">
                                ইমেইল
                            </Label>
                            <Input
                                type="email"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="email"
                                value={email}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="password">
                                পাসওয়ার্ড<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="password"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="password"
                                value={password}
                                required
                                invalid={error.password ? true : false}
                            />
                            {error.password && <FormFeedback>{error.password}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="confirm_password">
                                পাসওয়ার্ড নিশ্চিত করুন<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="password"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="confirm_password"
                                value={confirm_password}
                                required
                                invalid={error.confirm_password ? true : false}
                            />
                            {error.confirm_password && <FormFeedback>{error.confirm_password}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="address">
                                ঠিকানা
                            </Label>
                            <Input
                                type="address"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="address"
                                value={address}
                            />
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input
                                    type="checkbox"
                                    checked={owner}
                                    onChange={(e) => this.setState({ owner: e.target.checked })}
                                />{' '} মালিক
                                </Label>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input
                                    type="checkbox"
                                    checked={read_permission}
                                    onChange={(e) => this.setState({ read_permission: e.target.checked })}
                                />{' '} পড়ার অনুমতি
                                </Label>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input
                                    type="checkbox"
                                    checked={create_permission}
                                    onChange={(e) => this.setState({ create_permission: e.target.checked })}
                                />{' '} তৈরি অনুমতি
                                </Label>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input
                                    type="checkbox"
                                    checked={update_permission}
                                    onChange={(e) => this.setState({ update_permission: e.target.checked })}
                                />{' '} পরিবর্তনের অনুমতি
                                </Label>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input
                                    type="checkbox"
                                    checked={delete_permission}
                                    onChange={(e) => this.setState({ delete_permission: e.target.checked })}
                                />{' '} মোছার অনুমতি
                                </Label>
                        </FormGroup>


                        <Button
                            color=""
                            className="site-btn-dark mt-3"
                            block
                            type="submit"
                            disabled={loading}
                        >
                            {loading ? "Please Wait ..." : "Submit"}
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        )
    }
}


UserForm.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    getData: PropTypes.func.isRequired,
    editData: PropTypes.object,
    modalOpen: PropTypes.bool,
}


export default UserForm;
