import React, { Component } from 'react'
import { Row, Col, Button, Label } from 'reactstrap';
import { Form } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFilter, faTrash } from "@fortawesome/free-solid-svg-icons";
import PropTypes from 'prop-types'
import { Server } from '../../services'


let iniState = {
    status: '',
    payment_from_date: '',
    payment_to_date: '',
}

const server = new Server('/billing/')
class Search extends Component {
    state = iniState

    changeHandler = e => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    submitHandler = async e => {
        e.preventDefault()
        this.props.setStateHandler(true, [])

        let { status, payment_from_date, payment_to_date } = this.state

        let url = `/billing/search?status=${status}&payment_from_date=${payment_from_date}&payment_to_date=${payment_to_date}`


        let { data } = await server.get(url)
        this.props.setStateHandler(false, data ? data.results : [])
    }

    resetHandler = () => {
        this.setState(iniState)
        this.props.getData()
    }


    render() {
        let { status, payment_from_date, payment_to_date } = this.state


        return (
            <Row className='mb-5'>
                <Col>
                    <Label for='status'>পেমেন্টের অবস্থা</Label>
                    <Form.Control
                        as="select"
                        name="status"
                        id='status'
                        onChange={this.changeHandler}
                        value={status}
                    >
                        <option value=''>বাছাই করুন</option>
                        <option value='Paid'>পরিশোধ </option>
                        <option value='Partially'>আংশিকভাবে</option>
                        <option value='Unpaid'>অপরিশোধিত</option>
                    </Form.Control>
                </Col>
                <Col>
                    <Label for='payment_from_date'>পেমেন্টের তারিখ শুরু</Label>
                    <Form.Control
                        type="date"
                        name="payment_from_date"
                        value={payment_from_date}
                        onChange={this.changeHandler}
                    />
                </Col>
                <Col>
                    <Label for='payment_to_date'>পেমেন্টের তারিখ শেষ</Label>
                    <Form.Control
                        type="date"
                        name="payment_to_date"
                        value={payment_to_date}
                        onChange={this.changeHandler}
                    />
                </Col>
                <Col>
                    <div className="d-flex justify-content-between">
                        <span>
                            <Label for='submit' className='d-block'>&nbsp;</Label>
                            <Button
                                color="primary"
                                size="sm"
                                type='submit'
                                id='submit'
                                onClick={this.submitHandler}
                                title='বাছাই করুন'
                            >
                                <FontAwesomeIcon icon={faFilter} />
                            </Button>
                        </span>
                        <span>
                            <Label for='reset' className='d-block'>&nbsp;</Label>
                            <Button
                                color="dark"
                                size="sm"
                                type='reset'
                                id='reset'
                                onClick={this.resetHandler}
                                title='ক্লিয়ার করুন'
                            >
                                <FontAwesomeIcon icon={faTrash} />
                            </Button>
                        </span>
                    </div>
                </Col>
            </Row>
        )
    }
}


Search.propTypes = {
    getData: PropTypes.func.isRequired,
    setStateHandler: PropTypes.func.isRequired,
}


export default Search