import { getRequest, postRequest, putRequest } from '.'

import setAlertMessage from '../utils/setAlertMessage';


class Server {
    constructor(baseUrl) {
        this._baseUrl = baseUrl
    }

    // GET 
    async get(url = this._baseUrl) {
        return await getRequest(url)
    }

    // POST
    async set(formData, url = this._baseUrl) {
        try {
            let { isSuccess, data } = await postRequest(url, formData)
            if (isSuccess) {
                setAlertMessage('Added successfully!',)
                return data;
            }
            setAlertMessage('Something went wrong!', 'error')
            return false;
        } catch (error) {
            console.error(error)
            return false
        }
    }

    // UPDATE
    async update(formData, url = this._baseUrl, message = '') {
        try {
            let { isSuccess, data } = await putRequest(url, formData)
            if (isSuccess) {
                setAlertMessage(message ? message : 'Updated successfully!')
                return data;
            }
            setAlertMessage('Something went wrong!', 'error')
            return false;
        } catch (error) {
            console.error(error)
            return false
        }
    }

}
export default Server