import React, { Component } from 'react'
import { Table, Button } from 'reactstrap';
import { Server } from '../../services';
import UserForm from './UserForm';
import confirm from "reactstrap-confirm";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPenAlt, faTrashAlt, faPlus } from "@fortawesome/free-solid-svg-icons";
import { auth } from './../../utils/helper';




const server = new Server('/merchant_user_permission/')
class UserList extends Component {
    state = {
        loading: false,
        list: [],
        editData: {},
        modalOpen: false
    }

    componentDidMount() {
        this.getData()
    }

    getData = async () => {
        this.setState({ loading: true })

        try {
            let { data: { results } } = await server.get()

            this.setState({
                loading: false,
                list: results
            })
        } catch (error) {
            console.error(error)
        }
    }

    render() {
        let { list, loading, editData, modalOpen } = this.state

        return (
            <>
                <Button
                    color="dark"
                    size="sm"
                    className="mr-2"
                    onClick={() => this.setState({ modalOpen: true, editData: {} })}
                    title="নতুন ব্যবহারকারী যুক্ত করুন"
                >
                    <FontAwesomeIcon icon={faPlus} />
                </Button>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th>ক্রমিক নং</th>
                            <th>নাম</th>
                            <th>ফোন নম্বর</th>
                            <th>ইমেইল</th>
                            <th>ঠিকানা</th>
                            <th>অনুমতি</th>
                            <th width="10">কর্ম</th>
                        </tr>
                    </thead>
                    <tbody>
                        {loading ? <tr>
                            <td colSpan={10}>Loading...</td>
                        </tr> : list.length === 0 ? <tr><td colSpan='10'>তালিকায় কোনও আইটেম নেই।</td></tr> : list.map((item, i) => <tr key={item.id}>
                            <td>{i + 1}</td>
                            <td>{item.user.name}</td>
                            <td>{item.user.phone_number}</td>
                            <td>{item.user.email}</td>
                            <td>{item.user.address}</td>
                            <td>
                                {item.owner ? 'মালিক' : 'কর্মী'}<br />
                                {item.read_permission && 'পড়ার অনুমতি'}<br />
                                {item.create_permission && 'তৈরি অনুমতি'}<br />
                                {item.update_permission && 'পরিবর্তনের অনুমতি'}<br />
                                {item.delete_permission && 'মোছার অনুমতি'}<br />
                            </td>
                            <td>
                                {/* {item.status === 'Unpaid' &&
                                    <Button
                                        color="warning"
                                        size="sm"
                                        className="mr-2"
                                        onClick={async () => await confirm({ title: 'পেমেন্ট নিশ্চিত করা', message: "আপনি কি নিশ্চিত ভাবে পেমেন্ট করতে চান?", confirmText: "হ্যাঁ", cancelText: "না" }) && paymentHandler(item.id)}
                                    >
                                        <FontAwesomeIcon icon={faMoneyBillAlt} />
                                    </Button>} */}
                                {auth().permission.owner &&
                                    <Button
                                        color="dark"
                                        size="sm"
                                        className="mr-2"
                                        onClick={() => this.setState({ modalOpen: true, editData: item })}
                                        title="তথ্য পরিবর্তন করুন"
                                    >
                                        <FontAwesomeIcon icon={faPenAlt} />
                                    </Button>}
                            </td>
                        </tr>)}
                    </tbody>
                </Table>

                {modalOpen &&
                    <UserForm
                        editData={editData}
                        modalOpen={modalOpen}
                        getData={this.getData}
                        modalHandler={() => this.setState({ modalOpen: false, editData: {} })}
                    />}

            </>
        )
    }
}


export default UserList