const profileValidator = data => {
    let error = {}

    if (!data.name) {
        error.name = 'Name field is required!'
    }
    if (!data.phone_number) {
        error.phone_number = 'Phone number field is required!'
    }
    if (!data.password) {
        error.password = 'Password field is required!'
    }
    if (!data.confirm_password) {
        error.confirm_password = 'Confirm password field is required!'
    } else if (data.password !== data.confirm_password) {
        error.confirm_password = `confirm password doesn't match!`
    }

    return {
        isValid: Object.keys(error).length === 0 ? true : false,
        error
    }
}
export default profileValidator