import React, { useState } from 'react';
import { Table, Button } from 'reactstrap';
import PropTypes from 'prop-types'
import confirm from "reactstrap-confirm";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMoneyBillAlt, faPrint } from "@fortawesome/free-solid-svg-icons";
import { printDiv } from "../../utils/helper";
import OrderDetail from './OrderDetail';
import Checkout from './Checkout'
import DataPagination from '../common/DataPagination';



const DataTable = ({ stateData: { isLoading, list, buyer, next, previous }, getData }) => {
    const [editData, setEditData] = useState({})
    const [modalOpen, setModalOpen] = useState(false)

    const paymentHandler = (item) => {
        setEditData(item)
        setModalOpen(true)
    }

    return (
        <>
            <Table responsive striped bordered hover size="sm" className="text-center order-history">
                <thead>
                    <tr>
                        <th>ক্রমিক নং</th>
                        <th>মোট টাকা</th>
                        <th>টাকা প্রদানের তারিখ</th>
                        <th>পেমেন্ট বিবরণ</th>
                        <th>পেমেন্টের ধরন</th>
                        <th>অবস্থা</th>
                        <th>সংযুক্ত চেক</th>
                        <th>কর্ম</th>
                    </tr>
                </thead>
                <tbody>
                    {isLoading ? <tr>
                        <td colSpan={10}>Loading...</td>
                    </tr> : list.length === 0 ? <tr><td colSpan='10'>তালিকায় কোনও আইটেম নেই।</td></tr> : list.map((item, i) => <tr key={item.id}>
                        <td>{i + 1}</td>
                        <td>{item.amount}</td>
                        <td>{item.payment_date}</td>
                        <td>
                            {(item.payment_type === 'Partial' && (`আংশিক (${item.partial_amount})`)) || 'সম্পূর্ণ'}
                        </td>
                        <td>{item.bank_name && `${item.bank_name} || ${item.branch_name} || ${item.check_no} || ${item.check_date}`}</td>
                        <td>{item.status}</td>
                        <td>{item.attachment && <a href={item.attachment} target='_blank'>View</a>}</td>
                        <td>
                            {item.bank_name === null && item.status !== 'Paid' &&
                                <Button
                                    color="warning"
                                    size="sm"
                                    className="mr-2"
                                    title='পেমেন্ট করুন'
                                    onClick={async () => await confirm({ title: 'পেমেন্ট নিশ্চিত করা', message: "আপনি কি নিশ্চিত ভাবে পেমেন্ট করতে চান?", confirmText: "হ্যাঁ", cancelText: "না" }) && paymentHandler(item)}
                                >
                                    <FontAwesomeIcon icon={faMoneyBillAlt} />
                                </Button>}
                            <Button
                                color="dark"
                                size="sm"
                                className="mr-2"
                                title='প্রিন্ট করুন'
                                onClick={() => printDiv('order-detail-wrap')}
                            >
                                <FontAwesomeIcon icon={faPrint} />
                            </Button>
                            <OrderDetail
                                buyer={buyer}
                                selectedOrder={item}
                            />
                        </td>
                    </tr>)}
                </tbody>
            </Table>

            <DataPagination
                pageHandler={getData}
                next={next}
                previous={previous}
            />

            {modalOpen &&
                <Checkout
                    data={editData}
                    getData={getData}
                    modalHandler={() => setModalOpen(false)}
                />}

        </>
    );
}

DataTable.propTypes = {
    stateData: PropTypes.object.isRequired,
    getData: PropTypes.func.isRequired,
}

export default DataTable;
