import React, { useState } from 'react'
import { Table, Button } from 'reactstrap';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPenAlt } from "@fortawesome/free-solid-svg-icons";
import EditProfile from './EditProfile'
import { auth } from './../../utils/helper';


const ProfileInfo = ({ profile, getData }) => {
    const [modalOpen, setModalOpen] = useState(false)


    return (
        <>
            <Table responsive striped bordered hover size="sm" className="text-center">
                <thead>
                    <tr>
                        <th>আইডি নং</th>
                        <th>নাম</th>
                        <th>ফোন নম্বর</th>
                        <th>ইমেইল</th>
                        <th>ঠিকানা</th>
                        <th>ম্যানেজার তথ্য</th>
                        <th>চুক্তি</th>
                        <th>বাণিজ্য লাইসেন্স</th>
                        <th>টিনের শংসাপত্র</th>
                        <th>কর্ম</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>{profile.id}</td>
                        <td>{profile.name}</td>
                        <td>{profile.phone_number}</td>
                        <td>{profile.email}</td>
                        <td>{profile.address}</td>
                        <td>{profile.md_contact_details}</td>
                        <td>{profile.agreement}</td>
                        <td>{profile.trade_licence}</td>
                        <td>{profile.tin_certificate && <a href={profile.tin_certificate} target='_blank'>View</a>}</td>
                        <td>
                            {auth().permission.owner &&
                                <Button
                                    color="dark"
                                    size="sm"
                                    className="mr-2"
                                    onClick={() => setModalOpen(true)}
                                    title="তথ্য পরিবর্তন করুন"
                                >
                                    <FontAwesomeIcon icon={faPenAlt} />
                                </Button>}
                        </td>
                    </tr>
                </tbody>
            </Table>

            {modalOpen &&
                <EditProfile
                    profile={profile}
                    getData={getData}
                    modalHandler={() => setModalOpen(false)}
                />}

        </>
    )
}


ProfileInfo.propTypes = {
    profile: PropTypes.object.isRequired,
    getData: PropTypes.func.isRequired,
}
export default ProfileInfo