import React, { Component } from 'react';
import { Container, Card } from 'react-bootstrap';
import { Authenticate, Server } from '../../services';
import { TabContent, TabPane, Nav, NavItem, NavLink, } from 'reactstrap';
import EditProfile from './EditProfile';
import ChangePassword from './ChangePassword';
import ProfileInfo from './ProfileInfo';
import UserList from './UserList';



const server = new Server('/buyer/')
class Profile extends Component {
    state = {
        profile: {},
        loading: true,
        activeTab: 1
    }

    componentDidMount() {
        this.getData()
    }


    getData = async () => {
        this.setState({
            loading: true,
        })

        try {
            let { data } = await server.get()
            this.setState({
                profile: data.results[0] || {},
                loading: false
            })
        } catch (error) {
            console.error(error)
        }
    }

    render() {
        let { profile, loading, activeTab } = this.state


        return (
            <Container fluid={true} style={{ marginTop: 95 }}>
                <Card>
                    <Card.Header>
                        <Nav tabs>
                            <NavItem>
                                <NavLink
                                    className={activeTab === 1 ? 'active' : ''}
                                    onClick={() => this.setState({ activeTab: 1 })}
                                > তথ্যাবলী </NavLink>
                            </NavItem>
                            {/* <NavItem>
                                <NavLink
                                    className={activeTab === 2 ? 'active' : ''}
                                    onClick={() => this.setState({ activeTab: 2 })}
                                > তথ্য পরিবর্তন </NavLink>
                            </NavItem> */}
                            <NavItem>
                                <NavLink
                                    className={activeTab === 4 ? 'active' : ''}
                                    onClick={() => this.setState({ activeTab: 4 })}
                                >ব্যবহারকারীর তালিকা</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                    className={activeTab === 3 ? 'active' : ''}
                                    onClick={() => this.setState({ activeTab: 3 })}
                                > পাসওয়ার্ড পরিবর্তন </NavLink>
                            </NavItem>
                        </Nav>
                    </Card.Header>
                    <Card.Body>
                        <TabContent activeTab={activeTab}>
                            <TabPane tabId={1}>
                                {!loading && <ProfileInfo profile={profile} getData={this.getData} />}
                            </TabPane>
                            <TabPane tabId={3}>
                                {activeTab === 3 &&
                                    <ChangePassword />}
                            </TabPane>
                            <TabPane tabId={4}>
                                {activeTab === 4 &&
                                    <UserList />}
                            </TabPane>
                        </TabContent>
                    </Card.Body>
                </Card>
            </Container>
        );
    }
}

export default Profile;
