import React from "react";
import { Table } from "reactstrap";
import PropTypes from 'prop-types'


const OrderDetail = ({ buyer, selectedOrder }) => {
    let { order: { id, order_date, delivery_date, total_amount, status, order_items } } = selectedOrder

    return (
        <div id='order-detail-wrap' className='d-none'>
            <div className="section-padding text-center">
                <h4>{buyer.name}</h4>
                <p>{`${buyer.phone_number}, ${buyer.email}`}</p>
                <p>{buyer.address}</p>
            </div>


            {/* BILLING INFORMATION */}
            <h5>পেমেন্টের বিবরণ</h5>
            <Table responsive striped bordered hover size="sm" className="text-center order-history">
                <thead>
                    <tr>
                        <th>ক্রমিক নং</th>
                        <th>মোট টাকা</th>
                        <th>টাকা প্রদানের তারিখ</th>
                        <th>পেমেন্টের ধরন</th>
                        <th>পেমেন্ট বিবরণ</th>
                        <th>অবস্থা</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>1</td>
                        <td>{selectedOrder.amount}</td>
                        <td>{selectedOrder.payment_date}</td>
                        <td>
                            {(selectedOrder.payment_type === 'Partial' && (`আংশিক (${selectedOrder.partial_amount})`)) || 'সম্পূর্ণ'}
                        </td>
                        <td>{selectedOrder.bank_name && `${selectedOrder.bank_name} || ${selectedOrder.branch_name} || ${selectedOrder.check_no} || ${selectedOrder.check_date}`}</td>
                        <td>{selectedOrder.status}</td>
                    </tr>
                </tbody>
            </Table>



            {/* ORDER INFORMATION */}
            <hr />
            <h5>অর্ডার বিবরণ</h5>
            <Table responsive striped bordered hover size="sm" className="text-center">
                <thead>
                    <tr>
                        <th width="15">ক্রমিক নং</th>
                        <th>অর্ডারের তারিখ</th>
                        <th>বিতরণ তারিখ</th>
                        <th>মোট পণ্য</th>
                        <th>মোট দাম</th>
                        <th>অর্ডারের অবস্থা</th>
                    </tr>
                </thead>
                <tbody>
                    <tr key={id}>
                        <td>{id}</td>
                        <td>{order_date}</td>
                        <td>{delivery_date}</td>
                        <td>{order_items ? order_items.length : 0}</td>
                        <td>{total_amount}</td>
                        <td>{status}</td>
                    </tr>
                </tbody>
            </Table>

            <hr />
            <h5>পণ্যের বিবরণ</h5>
            <Table responsive striped bordered hover size="sm" className="text-center">
                <thead>
                    <tr>
                        <th width="15">ক্রমিক নং</th>
                        <th>কোড</th>
                        <th>পণ্যের নাম</th>
                        <th>ইউনিট</th>
                        <th>আনুমানিক হার (টাকা)</th>
                        <th>পরিমাণ</th>
                        <th>মোট (টাকা)</th>
                    </tr>
                </thead>
                <tbody>
                    {order_items && order_items.length > 0 && order_items.map((item, i) => <tr key={item.id}>
                        <td>{i + 1}</td>
                        <td>{item.product.product_code}</td>
                        <td>{item.product.name}</td>
                        <td>{item.unit}</td>
                        <td>{item.price}</td>
                        <td>{item.quantity}</td>
                        <td>{item.amount}</td>
                    </tr>)}
                </tbody>
            </Table>

            <div className="section-padding d-flex justify-content-between">
                <h6>Paid By: {selectedOrder.paid_by.name}</h6>
            </div>
        </div>
    )
}


OrderDetail.propTypes = {
    buyer: PropTypes.object.isRequired,
    selectedOrder: PropTypes.object.isRequired,
}

export default OrderDetail;
