import React from "react";
import { Modal } from "react-bootstrap";
import { Button, ModalFooter, Table } from "reactstrap";
import PropTypes from 'prop-types'
import { faPrint } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { printDiv } from "../../utils/helper";


const OrderDetailModal = ({ modalIsOpen, modalHandler, buyer, selectedOrder: { id, order_date, delivery_date, total_amount, status, order_items, created_by, confirmed_by } }) => {

    return (
        <Modal
            size="xl"
            dialogClassName="modal-90w"
            show={modalIsOpen}
            backdrop="static"
            scrollable={true}
            onHide={() => modalHandler()}
            id='order-detail-wrap'
        >


            <Modal.Header closeButton>
                <Modal.Title>অর্ডারের বিবরণী</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="section-padding text-center just-fro-print">
                    <h4>{buyer.name}</h4>
                    <p>{`${buyer.phone_number}, ${buyer.email}`}</p>
                    <p>{buyer.address}</p>
                </div>


                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">ক্রমিক নং</th>
                            <th>অর্ডারের তারিখ</th>
                            <th>বিতরণ তারিখ</th>
                            <th>মোট পণ্য</th>
                            <th>মোট দাম</th>
                            <th>অর্ডারের অবস্থা</th>
                            <th width="10" className='not-for-print'>কর্ম</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr key={id}>
                            <td>1</td>
                            <td>{order_date}</td>
                            <td>{delivery_date}</td>
                            <td>{order_items ? order_items.length : 0}</td>
                            <td>{total_amount}</td>
                            <td>{status}</td>
                            <td className='not-for-print'>
                                <Button color="dark" size="sm" onClick={() => printDiv('order-detail-wrap')} title='প্রিন্ট করুন'>
                                    <FontAwesomeIcon icon={faPrint} />
                                </Button>
                            </td>
                        </tr>
                    </tbody>
                </Table>

                <hr />
                <h5>পণ্যের বিবরণ</h5>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">ক্রমিক নং</th>
                            <th>কোড</th>
                            <th>পণ্যের নাম</th>
                            <th>ইউনিট</th>
                            <th>আনুমানিক হার (টাকা)</th>
                            <th>পরিমাণ</th>
                            <th>মোট (টাকা)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {order_items && order_items.length > 0 && order_items.map((item, i) => <tr key={item.id}>
                            <td>{i + 1}</td>
                            <td>{item.product.product_code}</td>
                            <td>{item.product.name}</td>
                            <td>{item.unit}</td>
                            <td>{item.price}</td>
                            <td>{item.quantity}</td>
                            <td>{item.amount}</td>
                        </tr>)}
                    </tbody>
                </Table>
                <div className="just-fro-print">
                    <div className="section-padding d-flex justify-content-between">
                        <h6>{created_by && `Created By: ${created_by.name}`}</h6>
                        <h6>{confirmed_by && `Confirmed By: ${confirmed_by.name}`}</h6>
                    </div>
                </div>
            </Modal.Body>
            <ModalFooter className='not-for-print'>
                <Button color="secondary" onClick={() => modalHandler()}>পেছনে</Button>
            </ModalFooter>
        </Modal>
    )
}


OrderDetailModal.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    selectedOrder: PropTypes.object.isRequired,
    modalIsOpen: PropTypes.bool,
    buyer: PropTypes.object.isRequired,
}

export default OrderDetailModal;
