const buyerValidator = data => {
    let error = {}

    if (!data.name) {
        error.name = 'Name field is required!'
    }
    if (!data.phone_number) {
        error.phone_number = 'Phone number field is required!'
    }
    if (!data.email) {
        error.email = 'Email field is required!'
    }
    // if (!data.tin_certificate) {
    //     error.tin_certificate = 'Tin certificate field is required!'
    // }
    // if (!data.tread_license) {
    //     error.tread_license = 'Tread license field is required!'
    // }

    return {
        isValid: Object.keys(error).length === 0 ? true : false,
        error
    }
}
export default buyerValidator