import React from 'react'
import { Pagination, PaginationItem } from 'reactstrap';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types'


const DataPagination = ({ pageHandler, next, previous }) => {

    return (
        <Pagination aria-label="Page navigation example">
            <PaginationItem onClick={() => previous && pageHandler(previous)} >
                <Link className='page-link' to='#blank'><span aria-hidden="true">‹</span></Link>
            </PaginationItem>


            <PaginationItem onClick={() => next && pageHandler(next)} >
                <Link className='page-link' to='#blank'><span aria-hidden="true">›</span></Link>
            </PaginationItem>
        </Pagination>
    )
}


DataPagination.propTypes = {
    pageHandler: PropTypes.func.isRequired,
    next: PropTypes.string,
    previous: PropTypes.string,
}

export default DataPagination