import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, FormGroup, Input, Label, FormFeedback } from "reactstrap";
import { Server } from "../../services";
import PropTypes from 'prop-types'
import { paymentValidator } from "../../validators";



let initState = {
    amount: 0,
    bank_name: '',
    branch_name: '',
    check_no: '',
    check_date: '',
    payment_type: '',
    partial_amount: 0,
    attachment: '',
    error: {},
    loading: true
}



const server = new Server('/billing/')

class Checkout extends Component {
    state = initState

    componentDidMount() {
        let { id, amount } = this.props.data

        this.setState({
            id,
            amount,
            loading: false
        })
    }


    changeHandler = (e) => {
        this.setState(prevState => {
            let { error } = prevState

            if (error[e.target.name])
                delete error[e.target.name]

            return {
                [e.target.name]: e.target.value,
                error
            }
        });
    };


    submitHandler = async (e) => {
        e.preventDefault();
        let validate = paymentValidator(this.state)
        if (!validate.isValid) {
            this.setState({ error: validate.error })
            return;
        }

        this.setState({ loading: true })

        let { id, amount, bank_name, branch_name, check_no, check_date, payment_type, partial_amount, attachment } = this.state

        const fd = new FormData()
        fd.append('amount', amount)
        fd.append('bank_name', bank_name)
        fd.append('branch_name', branch_name)
        fd.append('check_no', check_no)
        fd.append('check_date', check_date)
        fd.append('payment_type', payment_type)
        fd.append('partial_amount', partial_amount)
        fd.append('attachment', attachment)


        let response = await server.update(fd, `/billing/${id}/`, 'Payment request submitted successfully!')
        if (response) {
            this.setState(initState)
            this.props.modalHandler()
            this.props.getData()
            return;
        }
        this.setState({ loading: false })
    }

    fileChangeHandler = e => {
        this.setState({
            tread_license: e.target.files[0]
        })
    }




    render() {
        let { modalHandler } = this.props
        let { amount, bank_name, branch_name, check_no, check_date, payment_type, partial_amount, error, loading } = this.state


        return (
            <Modal
                size="md"
                dialogClassName="modal-90w"
                show={true}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>পেমেন্ট</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={this.submitHandler}>
                        <FormGroup>
                            <Label for="amount">
                                মোট টাকা<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="amount"
                                value={amount}
                                required
                                readOnly
                            />
                        </FormGroup>


                        <FormGroup>
                            <Label for="bank_name">
                                ব্যাংকের নাম<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="bank_name"
                                value={bank_name}
                                required
                                invalid={error.bank_name ? true : false}
                            />
                            {error.bank_name && <FormFeedback>{error.bank_name}</FormFeedback>}
                        </FormGroup>

                        <FormGroup>
                            <Label for="branch_name">
                                শাখার নাম<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="branch_name"
                                value={branch_name}
                                required
                                invalid={error.branch_name ? true : false}
                            />
                            {error.branch_name && <FormFeedback>{error.branch_name}</FormFeedback>}
                        </FormGroup>

                        <FormGroup>
                            <Label for="check_no">
                                চেক নম্বর<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="check_no"
                                value={check_no}
                                required
                                invalid={error.check_no ? true : false}
                            />
                            {error.check_no && <FormFeedback>{error.check_no}</FormFeedback>}
                        </FormGroup>

                        <FormGroup>
                            <Label for="check_date">
                                চেক তারিখ<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="date"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="check_date"
                                value={check_date}
                                required
                                invalid={error.check_date ? true : false}
                            />
                            {error.check_date && <FormFeedback>{error.check_date}</FormFeedback>}
                        </FormGroup>


                        <FormGroup>
                            <Label for="payment_type">
                                পেমেন্টের ধরন<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="select"
                                onChange={this.changeHandler}
                                name="payment_type"
                                value={payment_type}
                                invalid={error.payment_type ? true : false}
                                required
                            >
                                <option value=''>Select One</option>
                                <option value='Full'>সম্পূর্ণ</option>
                                <option value='Partial'>আংশিক</option>
                            </Input>
                            {error.payment_type && <FormFeedback>{error.payment_type}</FormFeedback>}
                        </FormGroup>


                        {payment_type === 'Partial' &&
                            <FormGroup>
                                <Label for="partial_amount">
                                    আংশিক পরিমাণ<span className="text-danger">*</span>
                                </Label>
                                <Input
                                    type="number"
                                    placeholder="Type here..."
                                    onChange={this.changeHandler}
                                    name="partial_amount"
                                    value={partial_amount}
                                />
                            </FormGroup>}

                        <FormGroup>
                            <Label for="attachment">
                                সংযুক্ত চেক করুন
                            </Label>
                            <Input
                                type="file"
                                name="file"
                                id='attachment'
                                invalid={error.attachment ? true : false}
                                onChange={(e) => this.setState({ attachment: e.target.files[0] })}
                                required
                            />
                            {error.attachment && <FormFeedback>{error.attachment}</FormFeedback>}
                        </FormGroup>

                        <Button
                            color=""
                            className="site-btn-dark"
                            block
                            type="submit"
                            disabled={loading}
                        >
                            {loading ? "Please Wait ..." : "Submit"}
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        )
    }
}


Checkout.propTypes = {
    getData: PropTypes.func.isRequired,
    modalHandler: PropTypes.func.isRequired,
    profile: PropTypes.object.isRequired
}


export default Checkout;
