const paymentValidator = data => {
    let error = {}

    if (!data.amount) {
        error.amount = 'Amount field is required!'
    }
    if (!data.bank_name) {
        error.bank_name = 'Bank name field is required!'
    }
    if (!data.branch_name) {
        error.branch_name = 'Branch name field is required!'
    }
    if (!data.check_no) {
        error.check_no = 'Cheque number field is required!'
    }
    if (!data.check_date) {
        error.check_date = 'Cheque date field is required!'
    }
    if (!data.payment_type) {
        error.payment_type = 'Payment type field is required!'
    } else if (!data.partial_amount) {
        error.partial_amount = 'Partial amount type field is required!'
    }
    if (!data.attachment) {
        error.attachment = 'Cheque attachment field is required!'
    }

    return {
        isValid: Object.keys(error).length === 0 ? true : false,
        error
    }
}
export default paymentValidator