import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, FormGroup, Input, Label, FormFeedback } from "reactstrap";
import { Server } from "../../services";
import PropTypes from 'prop-types'
import { buyerValidator } from "../../validators";



let initState = {
    name: '',
    email: '',
    phone_number: '',
    address: '',
    tread_license: '',
    tin_certificate: '',
    error: {},
    loading: true
}



const server = new Server('/buyer/')

class EditProfile extends Component {
    state = initState

    componentDidMount() {
        let { id, name, email, phone_number, address } = this.props.profile

        this.setState({
            id,
            name,
            email,
            phone_number,
            address,
            loading: false
        })
    }


    changeHandler = (e) => {
        this.setState(prevState => {
            let { error } = prevState

            if (error[e.target.name])
                delete error[e.target.name]

            return {
                [e.target.name]: e.target.value,
                error
            }
        });
    };


    submitHandler = async (e) => {
        e.preventDefault();
        let validate = buyerValidator(this.state)
        if (!validate.isValid) {
            this.setState({ error: validate.error })
            return;
        }

        this.setState({ loading: true })

        let { name, email, phone_number, address, id, tread_license, tin_certificate } = this.state

        const fd = new FormData()
        fd.append('name', name)
        fd.append('email', email)
        fd.append('phone_number', phone_number)
        fd.append('address', address)
        fd.append('tread_license', tread_license)
        fd.append('tin_certificate', tin_certificate)


        let response = await server.update(fd, `/buyer/${id}/`)
        if (response) {
            this.setState(initState)
            this.props.modalHandler()
            this.props.getData()
            return;
        }
        this.setState({ loading: false })
    }

    fileChangeHandler = e => {
        this.setState({
            tread_license: e.target.files[0]
        })
    }




    render() {
        let { modalHandler } = this.props
        let { name, email, phone_number, address, error, loading } = this.state


        return (
            <Modal
                size="md"
                dialogClassName="modal-90w"
                show={true}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>তথ্য পরিবর্তন</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={this.submitHandler}>
                        <FormGroup>
                            <Label for="name">
                                নাম<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="name"
                                value={name}
                                required
                                invalid={error.name ? true : false}
                            />
                            {error.name && <FormFeedback>{error.name}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="phone">
                                ফোন নম্বর<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="phone_number"
                                value={phone_number}
                                required
                                invalid={error.phone_number ? true : false}
                            />
                            {error.phone_number && <FormFeedback>{error.phone_number}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="email">
                                ইমেইল
                            </Label>
                            <Input
                                type="email"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="email"
                                value={email}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="address">
                                ঠিকানা
                            </Label>
                            <Input
                                type="address"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="address"
                                value={address}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="tread_license">
                                বাণিজ্য লাইসেন্স
                            </Label>
                            <Input
                                type="file"
                                name="file"
                                id='tread_license'
                                invalid={error.tread_license ? true : false}
                                onChange={(e) => this.setState({ tread_license: e.target.files[0] })}
                            />
                            {error.tread_license && <FormFeedback>{error.tread_license}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="tin_certificate">
                                টিনের শংসাপত্র
                            </Label>
                            <Input
                                type="file"
                                name="file"
                                id='tin_certificate'
                                invalid={error.tin_certificate ? true : false}
                                onChange={(e) => this.setState({ tin_certificate: e.target.files[0] })}
                            />
                            {error.tin_certificate && <FormFeedback>{error.tin_certificate}</FormFeedback>}
                        </FormGroup>
                        <Button
                            color=""
                            className="site-btn-dark"
                            block
                            type="submit"
                            disabled={loading}
                        >
                            {loading ? "Please Wait ..." : "Submit"}
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        )
    }
}


EditProfile.propTypes = {
    getData: PropTypes.func.isRequired,
    modalHandler: PropTypes.func.isRequired,
    profile: PropTypes.object.isRequired
}


export default EditProfile;
